import { useState } from 'react';
import AddItem from './ItemForm';
import Inventorytable from './Table';
import firebase from '../../../Config/FirebaseConfig';
import { useHistory } from 'react-router-dom';
import './inventory.css';
import {useNotifyContext} from "../../../context/notifyContext"
import { useWeb3React } from "@web3-react/core"
import { injected } from "../../../Components/Wallets/Connnectors"

//  initialized form fields
const itemInput = {
  firstName: '',
  lastName: '',
  email: '',
  number: '',
  brand: '',
  model: '',
  price: '',
  reference: ''
};

const Inventory = () => {
  const { set_notify,  set_loader,loader } = useNotifyContext()
  const history = useHistory();
  // All Others form Fields state
  const [inputValues, setInputValues] = useState(itemInput);
  // Product Image States
  const [imgUrl, setImgurl] = useState('');
  const [imgName, setImgName] = useState('');
  const [local, setLocal] = useState('');
  // Auth Certificate states
  const [pdfUrl, setIPdfUrl] = useState('');
  const [pdfName, setPdfName] = useState('');
  const [pdfLocal, setPdfLocal] = useState('');

  // const [address, setAddress] = useState('');
  // loading
  let [loading, setLoading] = useState(false);
  //  form component  state
  const [form, setForm] = useState(false);

  const { active, account, library, connector, activate, deactivate } = useWeb3React()

  async function connect() {
    try {
      await activate(injected)
    } catch (ex) {
      console.log(ex)
    }
  }

  // handle inputs change
  const handleItemsInp = (e) => {
    let { name, value } = e.target;
      setInputValues({ ...inputValues, [name]: value });
  };

  // select product image
  const handleWatchImg = (event) => {
    const url = URL.createObjectURL(event.target.files[0]);
    const itemImgName = event.target.files[0].name;
    setImgurl(url);
    setImgName(itemImgName);
    setLocal(event.target.files[0]);
  };

  // handle Auth Certificate
  const handlePdfSelect = (event) => {
    const url = URL.createObjectURL(event.target.files[0]);
    const itemName = event.target.files[0].name;
    setIPdfUrl(url);
    setPdfName(itemName);
    setPdfLocal(event.target.files[0]);
  };


  // firebase storage ref
  const storage = firebase.storage();
  let createStorageRef = () => storage.ref(`watchImages/${imgName}`).put(local);
  let downLoad = () => storage.ref(`watchImages/${imgName}`).getDownloadURL();
  let createStorageRef_PDF = () => storage.ref(`AuthCertificates/${pdfName}`).put(pdfLocal);
  let downLoad_PDF = () => storage.ref(`AuthCertificates/${pdfName}`).getDownloadURL();

  // firebase database reference
  const db = firebase.database();
  const itemsRef = db.ref(`Items`);

  // show add new form
  const handleTableAndForm = () => {
    setForm(true);
  };

  // go Back
  const handleBackTable = () => {
    setForm(false);
  };

  // store Product form data  to firebase Database
  const handleAddItem = (e) => {
    e.preventDefault();
    if (local || imgUrl || imgName) {
      if (pdfLocal || pdfUrl || pdfName) {
        setLoading(true)
        set_loader(true)
        createStorageRef().then(() => {
          downLoad().then((url) => {
            createStorageRef_PDF().then(() => {
              downLoad_PDF().then((url2) => {
                let key = itemsRef.push().key
                itemsRef.child(key).set({
                  inputValues,
                  url,
                  authCertificate: url2,
                  id: key
                })
                  .then(() => {
                    setLoading(false)
                    set_loader(false)
                    // history.push(`/dashboard`)
                    // setInputValues(itemInput)
                    // setForm(false)
                    // handleEmptyImg()
                    // handleEmptyPdf()
                    set_notify({open: true, msg: "Product posted Successfully!", type: "success"})
                  })
                  .catch((err) => {
                    console.log(err)
                    setLoading(false)
                    set_loader(false)
                    set_notify({open: true, msg: "item not added due to connection error", type: "error"})
                    // setInputValues(itemInput)
                    setImgurl('')
                  })
              })
            })
          })
        })
      } else {
        set_notify({open: true, msg: "Auth Certificate is required!", type: "error"})
      }
    }
    else {
      set_notify({open: true, msg: "Product Image is required!", type: "error"})
    }
  };

  // handle unSelect image file
  const handleEmptyImg = () => {
    setImgurl('')
    setImgName('')
    setLocal('')
  };

  // handle unSelect Auth Certificate file
  const handleEmptyPdf = () => {
    setIPdfUrl('')
    setPdfName('')
    setPdfLocal('')
  };

  return (
    <div className='inventoryContainer'>
      <button onClick={connect} className='cus_btn' >Connect Wallet</button>
        {active ? <span>Connected with <b>{account}</b></span> : <span>Not connected</span>}
      <br />
      {!form ? ( active ? 
            <button className='cus_btn' onClick={handleTableAndForm}>Add Item</button> : null
          ) : null}
      <div className="inventtable">
        {form ? (
          <AddItem
            handleAddItem={handleAddItem}
            handleItemsInp={handleItemsInp}
            handleWatchImg={handleWatchImg}
            handlePdfSelect={handlePdfSelect}
            inputValues={inputValues}
            imgUrl={imgUrl}
            pdfUrl={pdfUrl}
            local={local}
            loading={loading}
            backTable={handleBackTable}
            cross
            handleEmptyImg={handleEmptyImg}
            handleEmptyPdf={handleEmptyPdf}
          />
        ) : (
          <Inventorytable />
        )}
      </div>
    </div>
  )
};

export default Inventory;
