import { useState, useEffect } from 'react'
import { useSelector } from "react-redux";
import { useParams,useHistory } from "react-router-dom";
import {  Spinner } from "react-bootstrap";
import "./detail.css";
import {IoArrowBackCircleSharp} from "react-icons/io5";

const ProductDetail = () => {
    const { id } = useParams();
    const history = useHistory();
    const [loading, setLoading] = useState(true);
    const [details, setDetails] = useState(true);
    const itemSelector = useSelector((state) => {
        return state.watchReducer.items
    });

    // get product details
    useEffect(() => {
        if (id && itemSelector?.length > 0) {
            setLoading(false);
            let getItemDetails = itemSelector.find((v) => v.id === id);
            setDetails(getItemDetails)
        }
    }, [id, itemSelector])
    return (
        <div className="container">
            <div className="row py-3 mb-4">
                {!loading ?
                    <div className="col-12 p-0">
                        <div className="row m-0">
                            <div className="col-12 mb-2">
                                <IoArrowBackCircleSharp onClick={()=> history.goBack()} className="cursor-pointer mb-2 goBackIcon"/>
                                <h3>Brand: <span className="text-secondary">{details?.inputValues?.brand}</span></h3>
                            </div>
                            <div className="col-md-7 mb-3 col-12">
                                <img className="img-fluid detail_page_img" src={details.url} alt={details?.inputValues?.brand} />
                            </div>
                            <div className="col-md-5 col-12">
                                <h3 className="px-md-2">Other details: </h3>
                               <div className="d-flex justify-content-between align-items-center border rounded mx-md-2 px-3 py-2">
                                   <p className="fw-bold m-0">Price: </p>
                                   <p className="m-0 text-muted">${details?.inputValues?.price}</p>
                               </div>
                               <div className="d-flex mt-2 justify-content-between align-items-center border rounded mx-md-2 px-3 py-2">
                                   <p className="fw-bold m-0">Model: </p>
                                   <p className="m-0 text-muted">${details?.inputValues?.model}</p>
                               </div>
                               <div className="d-flex mt-2 justify-content-between align-items-center border rounded mx-md-2 px-3 py-2">
                                   <p className="fw-bold m-0">Reference: </p>
                                   <p className="m-0 text-muted">{details?.inputValues?.reference}</p>
                               </div>
                               <div className="d-flex mt-2 justify-content-between align-items-center border rounded mx-md-2 px-3 py-2">
                                   <p className="fw-bold m-0">Seller Name:</p>
                                   <p className="m-0 text-muted">{details?.inputValues?.firstName + " " + details?.inputValues?.lastName}</p>
                               </div>
                               <div className="d-flex mt-2 justify-content-between align-items-center border rounded mx-md-2 px-3 py-2">
                                   <p className="fw-bold m-0">Posted By: </p>
                                   <p className="m-0 text-muted">{details?.inputValues?.email}</p>
                               </div>
                               <div className="d-flex mt-2 justify-content-between align-items-center border rounded mx-md-2 px-3 py-2">
                                   <p className="fw-bold m-0">Phone: </p>
                                   <p className="m-0 text-muted">{details?.inputValues?.number}</p>
                               </div>
                            </div>
                        </div>
                        <div className="w-100"><hr /></div>
                    </div>
                    : <div className="col-12 py-4 my-4 text-center">
                        <Spinner animation="border" variant="dark" /><br /><br />
                        <h4>Loading...</h4>
                    </div>}
            </div>
        </div>
    )
}

export default ProductDetail
