import React from 'react'
import { Link } from 'react-router-dom'
import { IoMdArrowRoundBack } from 'react-icons/io'
import { MdAccountTree, MdOutlineAddAPhoto } from 'react-icons/md'
import { GoVerified } from 'react-icons/go'
import { useHistory } from 'react-router-dom';
import {uploadIPFS} from "../../../context/ipfs/upload";
import { useState } from 'react';
import {useNotifyContext} from "../../../context/notifyContext"

import Web3 from "web3";
import { useWeb3React } from '@web3-react/core';
import { AbiItem } from "web3-utils";
import ERC721MockArtifacts from "../../../Contracts/ERC721Mock.json";

const CONTRACT_ADDRESS = "0x1EF88F4ED90c7E56305b054ddB1cafB9458D3419";

const AddItem = ({
  handleAddItem,
  handleItemsInp,
  handleWatchImg,
  handlePdfSelect,
  inputValues,
  imgUrl,
  pdfUrl,
  local,
  loading,
  handleEmptyImg,
  handleEmptyPdf,
  cross,
  backTable,
  // form,
  backLink,
  editForm
}) => {
  const { account, library } = useWeb3React()
  // const history = useHistory();
  const { set_notify,  set_loader } = useNotifyContext();
  // loading
  let [status, setStatus] = useState(false);
  let [CID, setCID] = useState("");
  // console.log("++++++imgUrl++++++", imgUrl)
  // console.log("++++++pdfUrl++++++", pdfUrl)
  // console.log("++++++inputValues++++++", inputValues)
  // console.log("++++++loading++++++", loading)
  // console.log("=======local=======", local)
  // uploadIPFS()
  const handleUpload = async () => {
    
    if (local !== null && inputValues !== {}) {
      const ipfsHash = await uploadIPFS(local, inputValues);
      console.log(ipfsHash);
      setStatus(true);
      setCID(ipfsHash);
      set_notify({open: true, msg: "Watch Data uploaded Successfully!", type: "success"})
    }
    // console.log("======================");
    
  };

  const handleMint = async () => {
    const web3 = new Web3(Web3.givenProvider);
    // const web3_accounts = await web3.eth.getAccounts();
    // const web3_contract = new web3.eth.Contract(ERC721MockArtifacts.abi, CONTRACT_ADDRESS);
    const web3_contract = new web3.eth.Contract(ERC721MockArtifacts.abi, CONTRACT_ADDRESS);
    const gas = await web3_contract.methods.mintNFT(CID).estimateGas({ from: account });
    const tx = await web3_contract.methods.mintNFT(CID).send({
      from: account,
      gas
    });
    set_notify({open: true, msg: "Watch Minted Successfully!", type: "success"})
    backTable()
    console.log("###################3", CID, tx);
  };
  // history.push(`/dashboard`)

  return (
    <div className="create_goals_box">
      <div className="create_goals_inp">
        {backLink ?
          <Link to='/dashboard' className='icBtn'><IoMdArrowRoundBack /></Link>
          :
          <button className='icBtn' onClick={backTable}><IoMdArrowRoundBack /></button>
        }
        <form onSubmit={handleAddItem}>
          <div className="half_gaols_md">
            <div className="half_inp_goals">
              <div className="goals_inp">
                <p style={{ marginBottom: '10px', marginTop: '20px', fontWeight: 'bold' }}>Seller Info:</p>

                <label htmlFor="eventname">
                  <p> First Name:</p>
                  <input
                    type="text"
                    required
                    onChange={handleItemsInp}
                    name="firstName"
                    id="firstName"
                    value={inputValues.firstName}
                  />
                </label>
              </div>
              <div className="goals_inp ">
                <label htmlFor="dificulty">
                  <p>Last Name</p>
                  <input
                    type="text"
                    required
                    onChange={handleItemsInp}
                    name="lastName"
                    id="lastName"
                    value={inputValues.lastName}
                  />
                </label>
              </div>
              <div className="goals_inp">
                <label htmlFor="">
                  <p> Email:</p>
                  <input
                    type="email"
                    name="email"
                    required
                    onChange={handleItemsInp}
                    value={inputValues.email}
                  />
                </label>
              </div>
              <div className="goals_inp">
                <label htmlFor="">
                  <p>Phone Number: </p>
                  <input
                    type="text"
                    name="number"
                    required
                    onChange={handleItemsInp}
                    value={inputValues.number}
                  />
                </label>
              </div>
              <div className="goals_inp">
                <p style={{ marginBottom: '10px', marginTop: '20px', fontWeight: 'bold' }}>Watch Info:</p>
                <label htmlFor="">
                  <p> Brand</p>
                  <input
                    type="text"
                    name="brand"
                    required
                    onChange={handleItemsInp}
                    value={inputValues.brand}
                  />
                </label>
              </div>
              <div className="goals_inp">
                <label htmlFor="">
                  <p> Model</p>
                  <input
                    type="text"
                    name="model"
                    required
                    onChange={handleItemsInp}
                    value={inputValues.model}
                  />
                </label>
              </div>
              <div className="goals_inp">
                <label htmlFor="">
                  <p> Price</p>
                  <input
                    type="number"
                    name="price"
                    required
                    onChange={handleItemsInp}
                    value={inputValues.price}
                  />
                </label>
              </div>
              <div className="goals_inp">
                <label className="" htmlFor="">
                  <p> Reference</p>
                  <input
                    type="text"
                    name="reference"
                    required
                    onChange={handleItemsInp}
                    value={inputValues.reference}
                  />
                </label>
              </div>
            </div>
            <div className="right_hald_inp">
              <div className="goals_pic_upload">
                <p>Upload Photo:</p>
                {imgUrl ? (
                  <div className="crossImg">
                    {cross ? (
                      <button className="crosImg_btn" onClick={handleEmptyImg}>
                        x
                      </button>
                    ) : null}
                    <img className="uploaded_goal_img" src={imgUrl} alt="..." />
                  </div>
                ) : (
                  // <p className="empty"></p>
                  // null
                  <label className="cus_btnUpload" htmlFor="goalImg">
                    <input
                      type="file"
                      accept="image/*"
                      name="goalImg"
                      onChange={handleWatchImg}
                      style={{ display: 'none' }}
                      id="goalImg"
                    />
                    <p> Upload image</p>
                    <MdOutlineAddAPhoto className='iconss' />
                  </label>
                )}

                {pdfUrl ?
                  <div className="crossImg">
                    <button className="crosImg_btn" onClick={handleEmptyPdf}>
                      x
                    </button>
                    <iframe src={pdfUrl} frameborder="0"></iframe>
                  </div>
                  : <label className="cus_btnUpload" htmlFor="certImg">
                    <input
                      type="file"
                      accept=".pdf"
                      // name="goalImg"
                      onChange={handlePdfSelect}
                      style={{ display: 'none' }}
                      id="certImg"
                    />
                    <p> Upload Auth Cert</p>
                    <GoVerified className='iconss' />
                  </label>

                }
                <p>Note: only select pdf file for Auth Certificate</p>
              </div>
            </div>
          </div>
          <div className="addGoals_btn">
            {/* {!editForm && <>
            </>} */}
            <button className="btn btn-success mx-1" type="submit">Save</button>
            <button className="btn btn-primary mx-1" type="button" onClick={handleUpload} disabled={status}>Upload</button>
            <button className="btn btn-primary mx-1" type="button" onClick={handleMint} disabled={!status}>Mint</button>
          </div>
        </form>
      </div>
      {loading ? (
        <div className="addGoals_loading">
          <div className="addGoals_loading_md">
            {/* <CircularProgress /> */}
            <p>Loading...</p>
          </div>
        </div>
      ) : null}
    </div>
  )
}

export default AddItem
